import React, { Component } from 'react';

import './login.scss';

import { withRouter } from 'react-router-dom';

import axios from 'axios';

class login extends Component {
	constructor(props) {
		super(props);
		this.state = {
			email: '',
			password: '',
			isLoading: false
		};
		this.changeLogin = props.changeLogin;
	}

	setEamilValue = (event) => {
		let email = event.target.value;
		this.setState({ email: email });
	};

	setPasswordValue = (event) => {
		let password = event.target.value;
		this.setState({ password: password });
	};

	sendLoginRequest = (event) => {
		event.preventDefault();
		this.setState({ isLoading: true });
		const formData = new FormData();
		formData.append('email', this.state.email);
		formData.append('password', this.state.password);
		console.log(formData);
		axios
			.post('https://medicento-api.herokuapp.com/api/login', formData)
			.then((response) => {
				if (response.data.message === 'User Found') {
					this.changeLogin(true);
					this.setState({ isLoading: false });
					this.props.history.push('/');
				}
			})
			.catch((err) => {
				console.log(err);
				this.setState({ isLoading: false });
			});
	};

	render() {
		return (
			<div className="login">
				<div className="container">
					<form className="login_form" onSubmit={this.sendLoginRequest}>
						<div className="form-group">
							<label htmlFor="exampleInputEmail">Email address</label>
							<input
								type="email"
								className="form-control"
								onChange={this.setEamilValue}
								id="exampleInputEmail"
								placeholder="Enter email"
							/>
						</div>
						<div className="form-group">
							<label htmlFor="exampleInputPassword">Password</label>
							<input
								type="password"
								className="form-control"
								id="exampleInputPassword"
								onChange={this.setPasswordValue}
								placeholder="Password"
							/>
						</div>
						{this.state.isLoading ? (
							<div className="loader">
								<img
									src="https://wpamelia.com/wp-content/uploads/2018/11/ezgif-2-6d0b072c3d3f.gif"
									height="100"
									width="250"
									alt=""
								/>
							</div>
						) : null}
						<div className="form-group-submit">
							<input type="submit" value="Login" className="btn btn-primary" />
						</div>
					</form>
				</div>
			</div>
		);
	}
}

export default withRouter(login);
