import React, { Component } from 'react'

import { Link } from 'react-router-dom';

import './inventory_uploading.scss';
import { withRouter } from 'react-router-dom';
import Axios from 'axios';

class inventory_uploading extends Component {

    state = {
        selectedFile: null
    }

    submitForm = (event) => {
        event.preventDefault();
        const formData = new FormData();
        formData.append('name', 'Hello');
        formData.append('csv', this.state.selectedFile, this.state.selectedFile.name);
        Axios.post('http://localhost:3800/api/upload_csv', formData, {
            onUploadProgress: progressEvent => {
                console.log('Upload Progress' + Math.round(progressEvent.loaded / progressEvent.total * 100));
            }
        })
        .then(res => {
            console.log(res);
        })
        .catch(err => {
            console.log(err);
        })
    }

    fileSelectedHandler = (event) => {
        this.setState({
            selectedFile: event.target.files[0]
        })
    }

    render() {
        return (
            <div className="inventory_uploading">
                <div class="container" style={{ marginTop: '20px' }}>
                    <Link to="inventory_selection">
						<button className="btn btn-primary">Change Distributor</button>
					</Link>
                    <form onSubmit={this.submitForm} style={{ marginTop: '20px' }}>
                        <input name="file" id="file" type="file" accept=".csv" onChange={this.fileSelectedHandler}/>    
                        <input name="submit" id="submit" class="btn btn-primary" type="submit" value="Submit" />
                    </form>
                </div>
            </div>
        )
    }
}

export default inventory_uploading;