import React, { Component } from 'react';
import './change_distributor.scss';

import axios from 'axios';

import { Link } from 'react-router-dom';
class change_distributor extends Component {
	constructor(props) {
		super(props);
		this.state = {
			total_distributor_number: 0,
			upmapped_distributor_number: 0,
			states: [],
			cities: [],
			all_inventories: [],
			inventory: [],
			selected_city: {}
		};
	}

	componentDidMount() {
		axios
			.get('https://medicento-api.herokuapp.com/api/get_states')
			.then((response) => {
				this.setState({
					states: response.data.states,
					cities: response.data.cities,
					selected_city: response.data.cities[0]
				});
			})
			.catch((err) => {
				console.log(err);
			});
		axios
			.get('https://medicento-api.herokuapp.com/api/getDistributorbyCity')
			.then((response) => {
				this.setState({
					all_inventories: response.data.distributors,
					total_distributor_number: response.data.distributors.length,
					upmapped_distributor_number: response.data.distributors.length
				});
			})
			.catch((err) => {
				console.log(err);
			});
	}

	changeCity = (event) => {
		let id = event.target.value;
		axios
			.get('https://medicento-api.herokuapp.com/api/get_city_by_state?state_id=' + id)
			.then((response) => {
				this.setState({ cities: response.data.cities, selected_city: response.data.cities[0] });
			})
			.catch((err) => {
				console.log(err);
			});
	};

	changeDistributor = (name) => {
		let temp = [];
		for (let i = 0; i < this.state.all_inventories.length; i++) {
			if (this.state.all_inventories[i].city == name) {
				temp.push(this.state.all_inventories[i]);
			}
		}
		this.setState({ inventory: temp });
	};

	render() {
		const total_distributor_number = this.state.total_distributor_number;
		const upmapped_distributor_number = this.state.upmapped_distributor_number;

		const states = this.state.states;
		const cities = this.state.cities;

		let inventories = [];

		if (this.state.selected_city != undefined) {
			if (this.state.selected_city.name == 'Banglore') {
				inventories = this.state.all_inventories;
			}
		}

		return (
			<div className="distributor_selection">
				<h1 className="upper_heading">Mapping Inventory</h1>
				<div class="container">
					<div className="upper_heading_number">
						<div className="total_distributor d-inline-block">Total Distributor = {inventories.length}</div>
						<div className="upmapped_distributor d-inline-block">
							Distributors With Unmapped Inventory = {inventories.length}
						</div>
					</div>
					<br />
					<div className="select_state">
						<select name="state" id="state" onChange={this.changeCity}>
							<option value="" disabled>
								Select State
							</option>
							{states.map((state, i) => (
								<option value={state._id} key={i}>
									{state.name}
								</option>
							))}
						</select>
						<br />
						<br />
						<select name="city" id="city">
							<option value="" disabled>
								Select City
							</option>
							{cities.map((city, i) => (
								<option value={city._id} key={i}>
									{city.name}
								</option>
							))}
						</select>
						<div>
							<table className="table" style={{ marginTop: '10px' }}>
								<thead>
									<tr>
										<th>Distributor Name</th>
										<th>Edit Inventory</th>
									</tr>
								</thead>
								<tbody>
									{inventories.map((inv) => (
										<tr>
											<td>{inv.inventory_name}</td>
											<td>
												<Link to={'/inventory_mapping/' + inv.medi_code + '/'}>
													<button className="btn btn-primary">Edit</button>
												</Link>
											</td>
										</tr>
									))}
								</tbody>
							</table>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default change_distributor;
