import React, { Component } from 'react';

import { Link } from 'react-router-dom';

import './inventory_selection.scss';
import { withRouter } from 'react-router-dom';
import axios from 'axios';

import SnackBar from 'react-material-snackbar';

class inventory_selection extends Component {
	constructor(props) {
		super(props);
		this.state = {
			selected_inventory: props.match.params.id,
			inventory_master: [],
			temp_master: [],
			inventory_medicines: [],
			term: '',
			pageno: 0,
			pageno_i: 0,
			count_of_unmappped: 0,
			mapped: 'NotMapped',
			inputValues: [],
			isLoading: true,
			add: '',
			showSnakBar: false,
			message: ''
		};
	}

	componentWillMount() {
		axios
			.get('https://medicento-api.herokuapp.com/api/total_unmapped/' + this.state.selected_inventory)
			.then((response) => {
				this.setState({ count_of_unmappped: response.data.count });
				axios
					.get('https://medicento-api.herokuapp.com/api/get_all_master_medicines/')
					.then((response) => {
						this.setState({
							inventory_master: response.data.medicines,
							temp_master: response.data.medicines.slice(0, 10),
							isLoading: false
						});
					})
					.catch((err) => {
						console.log(err);
						this.setState({ isLoading: false });
					});
			})
			.catch((err) => {
				console.log(err);
				this.setState({ isLoading: false });
			});
	}

	searchMedicines = (event) => {
		event.preventDefault();
		let term = event.target.value;
		this.setState({ term: term });
		if (!this.state.isLoading) {
			this.setState({ isLoading: true });
			axios
				.post('https://medicento-api.herokuapp.com/api/get_medicines_for_key', {
					term: term,
					pagno: 0,
					name: this.state.selected_inventory
				})
				.then((response) => {
					console.log(response);
					this.setState({
						inventory_medicines: response.data.medicines,
						pageno_i: 1,
						isLoading: false,
						pageno: 1,
						inventory_master: response.data.inventory_master
					});
				})
				.catch((err) => {
					console.log(err);
					this.setState({ isLoading: false });
				});
		}
	};

	loadMore = () => {
		this.setState({ isLoading: true });
		axios
			.get('https://medicento-api.herokuapp.com/api/get_medicines_of_master/', {
				term: this.state.term,
				pagno: this.state.pageno
			})
			.then((response) => {
				this.setState({
					inventory_master: response.data.medicines,
					pageno: this.state.pageno + 1,
					isLoading: false
				});
			})
			.catch((err) => {
				console.log(err);
				this.setState({ isLoading: false });
			});
	};

	loadMoreInventory = () => {
		this.setState({ isLoading: true });
		axios
			.post('https://medicento-api.herokuapp.com/api/get_medicines_of_distributor/', {
				term: this.state.term,
				pagno: this.state.pageno_i,
				name: this.state.selected_inventory
			})
			.then((response) => {
				this.setState({
					inventory_medicines: response.data.medicines,
					pageno_i: this.state.pageno_i + 1,
					isLoading: false
				});
			})
			.catch((err) => {
				console.log(err);
				this.setState({ isLoading: false });
			});
	};

	submitSearch = (event) => {
		event.preventDefault();
		this.setState({ isLoading: true, pageno_i: 0, pageno: 0 });
		if (!this.state.isLoading) {
			this.setState({ isLoading: true });
			axios
				.post('https://medicento-api.herokuapp.com/api/get_medicines_for_key', {
					term: this.state.term,
					pagno: 0,
					name: this.state.selected_inventory
				})
				.then((response) => {
					this.setState({
						inventory_medicines: response.data.medicines,
						pageno_i: 1,
						isLoading: false,
						pageno: 1,
						inventory_master: response.data.inventory_master
					});
				})
				.catch((err) => {
					console.log(err);
					this.setState({ isLoading: false });
				});
		}
	};

	addProductId = (id, event) => {
		this.setState({ isLoading: true });
		event.preventDefault();
		console.log(this.state.inputValues[id]);
		axios
			.post('https://medicento-api.herokuapp.com/api/updateMedicines', {
				mapped_id: this.state.inputValues[id],
				master: this.state.inventory_master[id]._id,
				name: this.state.selected_inventory
			})
			.then((response) => {
				this.setState({ isLoading: false });
				this.setState({ isLoading: true, pageno_i: 0 });
				axios
					.post('https://medicento-api.herokuapp.com/api/get_medicines_of_distributor/', {
						term: this.state.term,
						pagno: 0,
						name: this.state.selected_inventory
					})
					.then((response) => {
						this.setState({
							inventory_medicines: response.data.medicines,
							pageno_i: 1
						});
						axios
							.post('https://medicento-api.herokuapp.com/api/get_medicines_of_master/', {
								term: this.state.term,
								pagno: 0
							})
							.then((response) => {
								this.setState({
									inventory_master: response.data.medicines,
									pageno: 1,
									isLoading: false
								});
							})
							.catch((err) => {
								console.log(err);
								this.setState({ isLoading: false });
							});
					})
					.catch((err) => {
						console.log(err);
						this.setState({ isLoading: false });
					});
			})
			.catch((err) => {
				console.log(err);
				this.setState({ isLoading: false });
			});
	};

	changeItem = (id, event) => {
		let inputValues = this.state.inputValues;
		inputValues[id] = event.target.value;
		this.setState({ inputValues: inputValues });
	};

	mapped = (event) => {
		this.setState({ isLoading: true });
		let value = event.target.value;
		console.log(value);
		axios
			.get('https://medicento-api.herokuapp.com/api/get_unmapped/' + value + '/' + this.state.selected_inventory)
			.then((response) => {
				this.setState(
					{
						inventory_medicines: response.data.medicines,
						mapped: value,
						isLoading: false,
						showSnakBar: true,
						message: 'Mapped'
					},
					() => {
						setTimeout(() => {
							this.setState({ showSnakBar: false });
						}, 3000);
					}
				);
			})
			.catch((err) => {
				console.log(err);
				this.setState({ isLoading: false });
			});
	};

	addMedicines = (event) => {
		event.preventDefault();
		let term = event.target.value;
		this.setState({ add: term });
	};

	addProd = (event) => {
		this.setState({ isLoading: true, showSnakBar: false });
		event.preventDefault();
		let name = this.state.add;
		axios
			.post('https://medicento-api.herokuapp.com/api/addMedicines/', { name: name })
			.then((response) => {
				this.setState({ isLoading: false, showSnakBar: true, message: response.data.message }, () => {
					setTimeout(() => {
						this.setState({ showSnakBar: false });
					}, 3000);
				});
			})
			.catch((err) => {
				console.log(err);
				this.setState({ isLoading: false, showSnakBar: true });
			});
	};

	render() {
		const inventory_medicines = this.state.inventory_medicines;
		const inventory_master = this.state.inventory_master;

		return (
			<div className="inventory_selection">
				<SnackBar show={this.state.showSnakBar} timer={1000}>
					<p>{this.state.message}</p>
				</SnackBar>
				<div style={{ margin: '20px' }}>
					<div className="card" style={{ padding: '10px', marginBottom: '10px' }}>
						<div className="row" style={{ marginLeft: '0px', marginRight: '0px' }}>
							<h3 className="col-md-10 px-0">
								{this.state.selected_inventory}
								<span className="badge" style={{ marginLeft: '20px', fontSize: '16px' }}>
									Total Unmapped -{' '}
									<span class="badge badge-success">{this.state.count_of_unmappped}</span>
								</span>
							</h3>
							<Link to="/inventory_selection" className="col-md-2 px-0">
								<button className="btn btn-primary">Change Distributor</button>
							</Link>
						</div>
					</div>
					{this.state.isLoading ? (
						<div className="loader">
							<img
								src="https://wpamelia.com/wp-content/uploads/2018/11/ezgif-2-6d0b072c3d3f.gif"
								height="100"
								width="250"
								alt=""
							/>
						</div>
					) : null}
					<div className="row" style={{ marginLeft: '0px', marginRight: '0px' }}>
						<div className="col-md-12 px-0 row" style={{ marginLeft: '0px', marginRight: '0px' }}>
							<div className="col-md-6  pl-0">
								<form
									onSubmit={this.submitSearch}
									className="card"
									style={{ padding: '10px', marginBottom: '10px' }}
								>
									<div class="input-group">
										<input
											type="text"
											placeholder="search medicine"
											class="form-control"
											onChange={this.searchMedicines}
										/>
										<div class="input-group-append">
											<input type="submit" value="Search" className="btn btn-primary" />
										</div>
									</div>
								</form>
							</div>
							<div className="col-md-6 pr-0">
								<form
									onSubmit={this.addProd}
									className="card col-md-12"
									style={{ padding: '10px', marginBottom: '10px' }}
								>
									<div class="input-group">
										<input
											type="text"
											placeholder="add medicine"
											class="form-control"
											onChange={this.addMedicines}
										/>
										<div className="input-group-append">
											<input type="submit" value="Add New" className="btn btn-danger" />
										</div>
									</div>
								</form>
							</div>
						</div>
						<div className="col-md-6">
							<div style={{ marginBottom: '10px' }}>
								<button className="btn btn-primary" onClick={this.loadMore}>
									Next
								</button>{' '}
								Page {this.state.pageno}
								<br />
							</div>
							<table className="table table-bordered">
								<thead>
									<tr>
										<th>Medicento Name</th>
										<th>Add</th>
									</tr>
								</thead>
								<tbody>
									{inventory_master.map((medicines, i) => (
										<tr>
											<td scope="row">{medicines.Item_name}</td>
											<td>
												<form
													action=""
													onSubmit={(e) => {
														this.addProductId(i, e);
													}}
												>
													<div class="input-group">
														<input
															type="text"
															name="item_code"
															class="form-control"
															onChange={(e) => this.changeItem(i, e)}
														/>
														<input
															type="submit"
															value="Submit"
															className="btn btn-success input-group-append"
														/>
													</div>
												</form>
											</td>
										</tr>
									))}
								</tbody>
							</table>
						</div>
						<div className="col-md-6">
							<select name="mapping" id="" onChange={this.mapped}>
								<option value="Mapped">Mapped</option>
								<option value="NotMapped" selected>
									Un Mapped
								</option>
							</select>
							<button
								className="btn btn-primary"
								onClick={this.loadMoreInventory}
								style={{ marginLeft: '10px' }}
							>
								Next
							</button>{' '}
							Page {this.state.pageno_i}
							<table className="table table-bordered" style={{ marginTop: '10px' }}>
								{inventory_medicines.length == 0 ? <h4>No Data</h4> : null}
								<thead>
									<tr>
										<th>Item Code</th>
										<th>Name</th>
										<th>PTR</th>
										<th>Manfacturer</th>
										<th>Status</th>
									</tr>
								</thead>
								<tbody>
									{inventory_medicines.map((medicines, i) => (
										<tr>
											<td>{medicines.item_code}</td>
											<td>{medicines.Item_name}</td>
											<td>{medicines.mrp}</td>
											<td>{medicines.manfc_name}</td>
											<td>{medicines.unmapped}</td>
										</tr>
									))}
								</tbody>
							</table>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default withRouter(inventory_selection);
