import React, { Component } from 'react';

import { Link } from 'react-router-dom';

class navbar extends Component {
	constructor(props) {
		super(props);
		this.state = {
			is_logged_in: props.is_logged_in
		};
		this.changeLogin = props.changeLogin;
	}

	render() {
		const is_logged_in = this.props.is_logged_in;

		return (
			<nav className="navbar navbar-expand-lg navbar-dark bg-dark">
				<Link to="/" className="navbar-brand">
					Medicento
				</Link>
				<button className="navbar-toggler">
					<span className="navbar-toggler-icon" data-toggle="collapse" data-target="#menu" />
				</button>
				<div className="collapse navbar-collapse" id="menu">
					{is_logged_in ? (
						<ul className="navbar-nav ml-auto">
							<li className="nav-item">
								<Link to="/inventory_selection" className="nav-link">
									Distributor Selection
								</Link>
							</li>
							<li className="nav-item">
								<Link to="/login" className="nav-link" onClick={() => this.changeLogin(false)}>
									Logout
								</Link>
							</li>
						</ul>
					) : (
						<ul className="navbar-nav ml-auto">
							<li className="nav-item">
								<Link to="/login" className="nav-link">
									Login
								</Link>
							</li>
						</ul>
					)}
				</div>
			</nav>
		);
	}
}

export default navbar;
